.navbar {
	border-radius: 0;
}

@media (min-width: 768px) {
	.navbar .navbar-collapse ul, .navbar .navbar-header {
		transform: none;
		transition: transform 1s;
	}

	.navbar.focused .navbar-collapse ul, .navbar.focused .navbar-header {
		transform: translateX(-100vw);
	}

	.navbar input, .navbar form div {
		width: 100% !important;
	}

	.navbar form {
		width: 20%;
		transition: width 1s;
	}

	.navbar.focused form {
		width: 100%;
	}

	.navbar #search {
		position: fixed;
		left: 5px;
		right: 5px;
		top: 5px;
		
	}
}